.header-color {
    background: #59C173;
    /* fallback for old browsers */
    background: -webkit-linear-gradient(to left, #5D26C1, #a17fe0, #59C173);
    /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to left, #5D26C1, #a17fe0, #59C173);
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.landing-grid {
    background: #59C173;
    /* fallback for old browsers */
    background: -webkit-linear-gradient(to left, #5D26C1, #a17fe0, #59C173);
    /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to left, #5D26C1, #a17fe0, #59C173);
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    text-align: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.projects-grid {
    display: flex;
}

/*resume page*/
.resume-right-col {
    background: #27221f;
    color: #fff;
    padding: 2em;
}

/*contact page*/
.contact-body {
    margin: auto;
    position: fixed;
    width: 100%;
    height: 100%;
    justify-content: center;
    padding-top: 2em;
}

.contact-grid {
    text-align: center;
    justify-content: center;
    width: 80%;
    height: 550px;
    background: #fff;
    color: #000;
    margin-bottom: 20px;
}

.contact-grid h2 {
    font-weight: bold;
}

.contact-grid hr {
    border-top: 5px dotted black;
    width: 70%;
    margin: auto;
}

.contact-list i {
    font-size: 60px;
    padding-right: 5rem;
}

/*contact page*/

.avatar-img {
    height: 200px;
    border-radius: 50%;
    margin-top: 5rem;
}

.banner-text {
    background-color: #000;
    opacity: .8;
    width: 75%;
    margin: auto;
    border-radius: 10px;
}

.banner-text h1 {
    color: #fff;
    font-size: 66px;
    font-weight: bold;
    text-align: center;
}

.banner-text hr {
    border-top: 5px dotted #fff;
    width: 50%;
    margin: auto;
}

.banner-text p {
    color: #fff;
    font-size: 24px;
    padding: 1em;
    text-align: center;
}

.social-links {
    display: flex;
    justify-content: space-between;
    width: 50%;
    margin: auto;
}

.social-links i {
    color: #fff;
    font-size: 5em;
}